<script>
/***
 * meta 
 */
import { useMeta } from "vue-meta";
/***
 * custom libraries
 */
import { scrollSmooth } from "@/utils/animate";

export default {
  name: "IAmStudent",
  setup() {
    useMeta({
      title: "I Am A Student",
      link: [
        { rel: "canonical", href: `${process.env.VUE_APP_BASEURL}/education/i-am-a-student` }
      ],
      meta: [{
        vmid: "description",
        name: "description",
        content: "Take Action For Your Future Self. Tremendous Opportunities in this New Economy for High School and College Graduates that will Lead to Satisfying Work and Solid Wages"
      }]
    })
  },
  data() {
    return {
      isTimer: null,
      VimeoIsLoaded: false,
    }
  },
  computed: {
    isActive() {
      return (this.$store.state.show.modal) ? "is-active" : "";
    }
  },
  unmounted() {
    this.isTimer = null
  },
  methods: {
    Close() {
      this.$store.commit("show/UpdShow", {cat: "modal", value: false});
      this.VimeoIsLoaded = false;
    },
    // open vimeo video modal
    Play() {
      this.VimeoIsLoaded = true;
      this.$store.commit("show/UpdShow", {cat: "modal", value: true});
      this.isTimer = window.setTimeout(function() {
        const elem = document.getElementById("vimeo-player");
        let widget = document.createElement("script")
        widget.setAttribute("src", "https://player.vimeo.com/api/player.js")
        widget.setAttribute("charset", "utf-8")
        elem.appendChild(widget)
        widget.onerror = () => {
          this.VimeoIsLoaded = false
        }
      }.bind(this), 100)
    },
    // animated scroll
    Scroll(e) {
      scrollSmooth(e.currentTarget.dataset.anchor);
    }
  }
}
</script>

